import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NzCodeBoxModule } from "app/showcase/share/nz-codebox/nz-codebox.module";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { UIUploadModule } from "app/custom-components/custom-components.module";
import { UIUploadDemoComponent } from "app/showcase/custom-components/ui-upload-demo/ui-upload-demo.component";
import { UIUploadDemoDemoPictureComponent } from "app/showcase/custom-components/ui-upload-demo/ui-upload-demo-picture.component";
import { UIUploadDemoDemoSingleFileComponent } from "app/showcase/custom-components/ui-upload-demo/ui-upload-demo-singleFile.component";
import { UIUploadDemoDemoMultipleFileComponent } from "app/showcase/custom-components/ui-upload-demo/ui-upload-demo-multipleFile.component";
import { UIUploadDemo1RoutingModule } from "app/showcase/custom-components/ui-upload-demo/ui-upload-demo.routing.module";

@NgModule({
	declarations:[
		UIUploadDemoComponent,
		UIUploadDemoDemoPictureComponent,
		UIUploadDemoDemoSingleFileComponent,
		UIUploadDemoDemoMultipleFileComponent
	],
	imports: [
		UIUploadDemo1RoutingModule,
		CommonModule,
		FormsModule,
		NzGridModule,
		NzToolTipModule,
		NzCodeBoxModule,
		UIUploadModule
	]
})
export class UIUploadDemoModule { }



