import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'ui-upload-demo-multipleFile',
  template: `
    <ui-upload type="file" [(ngModel)]="files" (fileChange)="fileChange($event)"></ui-upload>
    <p>{{fileChangeList}}</p>
  `,
  styles: []
})
export class UIUploadDemoDemoMultipleFileComponent implements OnInit {

  files: any[] = [];
  fileChangeList: any;

  constructor() { }

  ngOnInit() {
  }

  fileChange($event) {
    this.fileChangeList = JSON.stringify($event);
  }

}



